.app {
    width: 100vw;
    height: 100vh;
    display: grid;

    grid-template-areas: "header" "content" "footer";
    grid-template-rows: 10% 90% 0%;
    grid-template-columns: 1fr;
    overflow: hidden;
}

.app-meet-me-now-content {
    height: 93vh;
    grid-area: content;
    background-color: white;
    overflow: hidden;
}

.createEventButton {
    position: absolute;
    bottom: 50px;
    right: 25px
}