.eventWrapper {
    width: 100%;
    height: 100%;
}

.contentWrapper {
    width: 100%;
    height: 100%;

    display: grid;
    grid-template-areas: "i i m m" "c c m m";
    grid-template-columns: 25% 25% 25% 25%;
    grid-template-rows: 50% 50%;
}

.infoWrapper {
    padding-top: 5%;
    padding-left: 10px;
    grid-area: i;
    background-color: white;
}

.mapWrapper {
    grid-area: m;
    background-color: white;
}

.mapContentWrapper {
    width: 90%;
    height: 90%;
    margin: 5%;
}

.commentsWrapper {
    grid-area: c;
    padding-left: 10px;
    background-color: white;
}

.commentField {
    width: 100%;
    background-color: white;
}

.commentsContent {
    height: 77.5%;
    margin-top: 10px;
    width: 100%;
    background-color: white;
    overflow: auto;
}

