.autocompleteHelperUl {
    background-color: white;
    border-radius: 15px;
    margin-top: 5px;
    padding-top: 5px;
    padding-bottom: 5px;
}

.autocompleteHelperLi {
    cursor: pointer;
    list-style-type: none;
    border-radius: 5px;
    margin-bottom: 5px;
    margin-top: 5px;
    position: relative;
    left: -20px;
}

.autocompleteHelperLi:hover {
    border: 1px solid #1976d2;
}