.mainWrapper {
    width: 100%;
    height: 100%;
    background-color: white;

    position: relative;
}

.formWrapper {
    width: 400px;
    height: 500px;
    transition: height 0.3s;

    border-radius: 15px;
    background-color: white;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.5);

    position: absolute;
    left: 50%;
    top: 50%;

    margin-top: -250px;
    margin-left: -200px;
}

.registrationWrapper {
    width: 97%;
    height: 100%;

    display: grid;
    grid-template-areas: "n n a a" "n n a a" "e e e e";
    grid-template-columns: 25% 25% 25% 25%;
    grid-template-rows: 25% 25% 50%;
}

.nameSurnameWrapper {
    grid-area: n;
    margin-top: 10px;
}

.avatarWrapper {
    grid-area: a;
    margin-top: 10px;
}

.avatarImageWrapper {
    width: 101px;
    margin-left: auto;
    margin-right: auto;
}

.avatarButtonWrapper {
    margin-left: auto;
    margin-right: auto;
    width: 75px;
    margin-top: 10px;
}

.emailPasswordWrapper {
    grid-area: e;
}

.signInWrapper {
    position: absolute;
    top: 50%;
    transform: translate(0, -50%)
}