.eventCreatorWrapper {
    height: 100%;
    width: 100%;
}

.formWrapper {
    transition: width 1s, height 1s;
}

.mapPageWrapper {
    width: 100%;
    height: 100%;

    display: grid;
    grid-template-areas: "m m m" "a a b" ;
    grid-template-columns: 33% 33% 33%;
    grid-template-rows: 90% 10%;
}

.mapWrapper {
    grid-area: m;
}

.mapButtonsWrapper {
    grid-area: b;
    margin-top: 8%;
}

.autocompleteWrapper {
    margin-top: 2.5%;
    grid-area: a;
}

.eventPreviewWrapper {
    width: 100%;
    height: 100%;

    display: grid;
    grid-template-areas: "i" "m" "b";
    grid-template-columns: 100%;
    grid-template-rows: 40% 40% 20%;
}


.eventPreviewInfoWrapper {
    grid-area: i;
    overflow: hidden;
}

.eventPreviewMapWrapper {
    grid-area: m;
}

.eventPreviewButtonsWrapper {
    grid-area: b;
}

.buttonsWrapper {
    position: absolute;
    right: 5%;
    bottom: 5%;
    display: inline-flex;
}

.uploadEventWallpaper {
    width: 100%;
}

