.eventInfoWrapper {
    width: 100%;
    height: 90%;

    display: grid;
    grid-template-areas: "i a" "d d";
    grid-template-columns: 50% 50%;
    grid-template-rows: 70% 30%;
}

.infoWrapper {
    grid-area: i;
}

.avatarWrapper {
    grid-area: a;
    margin: auto;
}

.descriptionWrapper {
    grid-area: d;
}
